import { useContext } from "react";
import { ShopContext } from "../context";

import { Good } from "./Goodsitem";
import { Preloader } from "./Preloader";

function GoodsList() {
  const { goods = [] } = useContext(ShopContext);

  if (!goods.length) {
    return (
      <>
        <Preloader />
        <p>Nothing did load :(</p>
      </>
    );
  }

  return (
    <div className="goods">
      {goods.map((item) => (
        <Good key={item.id} {...item} />
      ))}
    </div>
  );
}
export { GoodsList };
