export function reducer(state, { type, payload }) {
  switch (type) {
    case "GET_GOODS": {
      return {
        ...state,
        goods: payload || [],
        loading: false,
      };
    }
    case "CLOSE_ALERT":
      return {
        ...state,
        alertShow: "",
      };

    case "ORDER_GOOD": {
      const orderIndex = state.order.findIndex(
        (items) => items.id === payload.id
      );

      let newOrder = null;

      if (orderIndex < 0) {
        const newItem = {
          ...payload,
          quantity: 1,
        };
        newOrder = [...state.order, newItem];
      } else {
        newOrder = state.order.map((item, index) => {
          if (index === orderIndex) {
            return {
              ...item,
              quantity: item.quantity + 1,
            };
          } else {
            return item;
          }
        });
        localStorage.setItem("orders", JSON.stringify(newOrder));
      }
      return {
        ...state,
        order: newOrder,
        alertShow: payload.name,
      };
    }
    case "PLUS_QUANTITY": {
      const orderIndex = state.order.findIndex(
        (items) => items.id === payload.id
      );
      const newItems = state.order.map((item, index) => {
        if (index === orderIndex) {
          return {
            ...item,
            quantity: item.quantity + 1,
          };
        } else {
          return item;
        }
      });
      localStorage.setItem("orders", JSON.stringify(newItems));
      return {
        ...state,
        order: newItems,
      };
    }
    case "MINUS_QUANTITY": {
      const orderIndex = state.order.findIndex(
        (items) => items.id === payload.id
      );
      const newItems = state.order.map((item, index) => {
        if (index === orderIndex) {
          const newQuantity = item.quantity - 1;
          return {
            ...item,
            quantity: newQuantity >= 1 ? item.quantity - 1 : 1,
          };
        } else {
          return item;
        }
      });
      localStorage.setItem("orders", JSON.stringify(newItems));
      return {
        ...state,
        order: newItems,
      };
    }
    case "HANDLE_BASKET":
      return {
        ...state,
        doesBasketShow: !state.doesBasketShow,
      };
    case "REMOVE_GOOD":
      return {
        ...state,
        order: state.order.filter((goods) => goods.id !== payload.id),
      };

    default:
      return state;
  }
}
