import { createContext, useReducer } from "react";
import { reducer } from "./reducer";

export const ShopContext = createContext();

const initialState = {
  goods: [],
  loading: true,
  order: [],
  doesBasketShow: false,
  alertShow: "",
};

export const ContextProvider = ({ children }) => {
  const [value, dispatch] = useReducer(reducer, initialState);

  value.orderGood = (item) => {
    dispatch({ type: "ORDER_GOOD", payload: item });
  };
  value.removeGood = (itemId) => {
    dispatch({ type: "REMOVE_GOOD", payload: { id: itemId } });
  };
  value.plusQuantity = (item) => {
    dispatch({ type: "PLUS_QUANTITY", payload: item });
  };
  value.minusQuantity = (item) => {
    dispatch({ type: "MINUS_QUANTITY", payload: item });
  };
  value.handleBasket = () => {
    dispatch({ type: "HANDLE_BASKET" });
  };
  value.closeAlert = () => {
    dispatch({ type: "CLOSE_ALERT" });
  };
  value.setGoods = (data) => {
    dispatch({ type: "GET_GOODS", payload: data });
  };

  return <ShopContext.Provider value={value}>{children}</ShopContext.Provider>;
};
